.section-cards {
    padding-block: 48px 136px;
    @media (max-width: 840px) {
        padding-top: 70px;
    }

    &__title {
        margin-inline: auto;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        @media (max-width: 840px) {
            flex-direction: column;
            gap: 50px;
        }
    }

    &__item {
        max-width: 380px;
        display: flex;
        flex-direction: column;
        align-items: center;
        @media (max-width: 840px) {
            max-width: 100%;
        }
    }

    &__image {
        margin-bottom: 30px;
    }

    &__item-title {
        margin-bottom: 30px;
        @media (max-width: 1040px) {
            margin-bottom: 20px;
        }
    }

    &__text {
        margin-top: auto;
        text-align: center;
    }

    &__image,
    &__item-title,
    &__text {
        position: relative;
        z-index: 1;
    }
}
