.header {
  padding-block: 31px 15px;
  position: relative;
  @media (max-width: 540px) {
    padding-bottom: 40px;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: -30px;
    right: -30px;
    background-image: url(../images/gradient-1.png);
    background-repeat: no-repeat;
    background-position: top;
    height: 500px;
    @media (max-width: 840px) {
      display: none;
    }
  }

  &__nav {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @media (max-width: 650px) {
      align-items: center;
    }
  }

  &__logo {
    margin-left: -40px;
    margin-right: 15px;
  }

  &__buttons {
    display: flex;
    align-items: center;
    gap: 30px;

    @media (max-width: 768px) {
      width: 0px;
      transform: translateY(-400px);
      transition: transform 0.4s ease;

      .header__button-login,
      .header__button-reg {
        font-size: 0;
        padding: 0;
      }

      &--active {
        justify-content: center;
        overflow-y: auto;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
        width: 100%;
        height: 20vh;
        background-color: rgba(#000, 0.8);
        transform: translateY(0px);

        .header__button-login {
          font-size: 16px;
          padding: 16px 45px;
        }
        .header__button-reg {
          font-size: 16px;
          padding: 16px 36px;
        }
      }
    }
    @media (max-width: 540px) {
      &--active {
        height: 35vh;
      }
    }
    @media (max-width: 380px) {
      &--active {
        height: 50vh;
        flex-direction: column;

        .header__button {
          width: 80%;
          text-align: center;
          font-size: 20px;
        }
      }
    }
  }

  &__button {
    border-radius: 40px;
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
    color: var(--main-color-text);
  }

  &__button-login {
    background: linear-gradient(270deg, #004aad 0%, #26a9e0 99.99%, #26a9e0 100%);
    padding: 16px 45px;
  }

  &__button-reg {
    border: 2px solid var(--accent-color);
    padding: 16px 36px;
  }
}

.burger-menu {
  border: none;
  background-color: transparent;
  display: block;
  height: 28px;
  width: 40px;
  position: relative;
  cursor: pointer;
  z-index: 21;
  @media (min-width: 769px) {
    display: none;
  }
  span {
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    width: 100%;
    height: 3px;
    background-color: var(--main-color-text);
    transition: all 0.2s ease 0s;
    &:first-child {
      top: 0;
    }
    &:last-child {
      top: auto;
      bottom: 0;
    }
  }
  &--active {
    span {
      transform: scale(0);
      &:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px);
      }
      &:last-child {
        transform: rotate(45deg);
        bottom: calc(50% - 1px);
      }
    }
  }
}
