.deal {
    position: relative;

    &::before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: -30px;
        right: -30px;
        background-image: url("../images/gradient-4.png");
        background-repeat: no-repeat;
        background-position: top center;
        height: 800px;

        @media (max-width: 375px) {
            top: 10px;
            background-image: url("../images/gradient-mobile-5.png");
            height: 440px;
        }
    }

    &::after {
        @media (max-width: 375px) {
            content: "";
            position: absolute;
            z-index: -1;
            bottom: 190px;
            left: -30px;
            right: -30px;
            background-image: url("../images/gradient-mobile-6.png");
            background-repeat: no-repeat;
            background-position: top center;
            height: 505px;
        }
    }

    &__list {
        @media (max-width: 840px) {
            gap: 38px;
        }
    }

    &__number {
        font-size: 230px;
        line-height: 276px;
        background: linear-gradient(180deg, #26a9e0 0%, #004aad 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }
    &__title {
        max-width: 376px;
        margin-bottom: 55px;
    }
}
