@import "fonts";
html,
body {
    height: 100%;
    scroll-behavior: smooth;
}
.body {
    
    font-family: "Open Sans", "sans-serif";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--main-color-text);
    background-color: var(--background-color);
    min-width: 280px;
    max-width: 1440px;
    margin-inline: auto;
    overflow-x: hidden;
    // @media (max-width: 768px) {
    //     font-size: 18px;
    // }
    // @media (max-width: 540px) {
    //     font-size: 16px;
    // }
}
.body--lock {
    overflow: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

.visibility-hidden {
    opacity: 0;
    visibility: hidden;
    font-size: 0;
}

.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.main {
    flex: 1 1 auto;
    position: relative;
    &::before {
        content: "";
        position: absolute;
        z-index: 1;
        bottom: 755px;
        left: -30px;
        right: -30px;
        background-image: url("../images/gradient-3.png");
        background-repeat: no-repeat;
        background-position: top center;
        height: 1600px;
        
        @media (max-width: 992px) {
            bottom: 1005px;
        }
        @media (max-width: 840px) {
            bottom: 1805px;
        }
        @media (max-width: 375px) {
            display: none;
        }
    }
}

.container {
    max-width: 1260px;
    padding-inline: 30px;
    margin-inline: auto;
}

h1,
h2 {
    span {
        background-color: var(--accent-color);
        padding: 5px;
    }
}

.title {
    font-weight: 700;
    font-size: 30px;
    line-height: 50px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    @media (max-width: 610px) {
        font-size: 24px;
        margin-bottom: 49px;
    }
    @media (max-width: 480px) {
        font-size: 22px;
    }
}

.title-mini {
    font-weight: 400;
    font-size: 22px;
    line-height: 36px;
    text-align: center;
    text-transform: uppercase;
    @media (max-width: 1040px) {
        font-size: 20px;
        line-height: 30px ;
    }
}

.button,
.button-accent {
    border-radius: 40px;
    font-weight: 400;
    font-size: 22px;
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    width: 300px;
    padding-block: 26px;

    @media (max-width: 1200px) {
        font-size: 20px;
        width: 250px;
    }
    @media (max-width: 992px) {
        font-size: 18px;
        width: 200px;
        padding-block: 20px;
    }
}

.button {
    border: 2px solid var(--accent-color);
}

.button-accent {
    border: none;
    background: linear-gradient(270deg, #004AAD 0%, #26A9E0 99.99%, #26A9E0 100%);
}
