.section-form {
    position: relative;
    padding-block: 40px 230px;
    @media (max-width: 992px) {
        padding-bottom: 120px;
    }
    &::before {
        @media (max-width: 375px) {
            content: "";
            position: absolute;
            z-index: 0;
            top: -160px;
            left: -30px;
            right: -30px;
            background-image: url("../images/gradient-mobile-3.png");
            background-repeat: no-repeat;
            background-position: top center;
            height: 618px;
        }
    }

    &::after {
        @media (max-width: 375px) {
            content: "";
            position: absolute;
            z-index: 0;
            bottom: 190px;
            left: -30px;
            right: -30px;
            background-image: url("../images/gradient-mobile-4.png");
            background-repeat: no-repeat;
            background-position: top center;
            height: 456px;
        }
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;

        @media (max-width: 992px) {
            flex-direction: column;
        }
    }

    &__info {
        max-width: 512px;
        @media (max-width: 992px) {
            order: -1;
            max-width: 577px;
            text-align: center;
            margin-bottom: 70px;
        }
    }

    &__title {
        position: relative;
        z-index: 1;
        text-align: left;
        margin-bottom: 46px;
        @media (max-width: 992px) {
            text-align: center;
        }
    }

    &__text {
        position: relative;
        z-index: 1;
        margin-bottom: 23px;
    }

    &__link {
        display: inline-block;
        width: 300px;
        border-radius: 40px;
        border: 2px solid transparent;
        background-color: var(--background-color);
        font-weight: 400;
        font-size: 22px;
        color: #ffffff;
        text-align: center;
        text-transform: uppercase;
        width: 300px;
        padding-block: 24px;
        margin-top: 37px;
        position: relative;
        @media (max-width: 992px) {
            font-size: 18px;
        }
        @media (max-width: 540px) {
            width: 100%;
            padding-block: 17px;
        }

        &::before {
            content: "";
            margin: -3px;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: -1;
            border-radius: inherit;
            background: linear-gradient(180deg, #26a9e0 0%, #004aad 100%);
        }

        span {
            position: relative;
            z-index: 1;
        }
    }
}

.form {
    max-width: 600px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-right: 87px;
    position: relative;
    @media (max-width: 992px) {
        padding-right: 0;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg, #26a9e0 0%, #004aad 100%);

        @media (max-width: 992px) {
            display: none;
        }
    }

    &__content {
        width: 100%;
        display: flex;
        flex-direction: column;
        @media (max-width: 992px) {
            align-items: center;
            padding-right: 0;
        }
    }

    &__button {
        padding-block: 22px;
        margin-top: 46px;

        @media (max-width: 992px) {
            width: 300px;
            margin-inline: auto;
        }
        @media (max-width: 540px) {
            width: 100%;
            padding-block: 17px;
        }
    }

    &__row {
        border-color: 2px solid transparent;
        border-radius: 30px;
        background-color: var(--background-color);
        max-width: 513px;
        width: 100%;
        position: relative;
        &:not(:last-child) {
            margin-bottom: 28px;
        }

        &::before {
            content: "";
            margin: -2px;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: -1;
            border-radius: inherit;
            background: linear-gradient(180deg, #26a9e0 0%, #004aad 100%);
        }
    }

    &__label-select {
        position: relative;

        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 30px;
            transform: translateY(-50%);
            background-image: url("../images/icons/arrow-down.svg");
            background-repeat: no-repeat;
            width: 24px;
            height: 22px;
            pointer-events: none;
        }
    }

    &__select {
        cursor: pointer;
        border: 2px solid transparent;
        border-color: transparent;
        border-radius: 30px;
        width: 513px;
        height: 56px;
        background-color: transparent;
        padding: 15px 30px;

        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
    }
    &__select::-ms-expand {
        display: none;
    }

    &__select > option {
        color: #000;
    }

    &__input {
        background-color: transparent;
        border-color: transparent;
        border-radius: 30px;
        font-family: "Open Sans", "sans-serif";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        color: var(--main-color-text);

        max-width: 513px;
        width: 100%;
        height: 56px;
        padding: 15px 30px;

        &::placeholder {
            font-family: "Open Sans", "sans-serif";
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            color: var(--main-color-text);
        }
    }

    &__select,
    &__input {
        position: relative;
        z-index: 1;
    }
}

.itc-select {
    position: relative;
    z-index: 1;
    width: 100%;
}

.itc-select__toggle {
    font-family: "Open Sans", "sans-serif";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    cursor: pointer;
    user-select: none;
    text-align: left;
    border: 2px solid transparent;
    border-radius: 30px;
    color: var(--main-color-text);
    background-color: transparent;
    max-width: 513px;
    width: 100%;
    height: 56px;
    padding: 15px 30px;
}

