.benefits {
    position: relative;
    @media (max-width: 840px) {
        padding-bottom: 120px;
    }

    &::before {
        content: "";
        position: absolute;
        z-index: 1;
        top: 0;
        left: -30px;
        right: -30px;
        background-image: url("../images/gradient-2.png");
        background-repeat: no-repeat;
        background-position: top center;
        height: 800px;

        @media (max-width: 375px) {
            top: 250px;
            background-image: url("../images/gradient-mobile-1.png");
            height: 675px;
        }
    }

    &::after {
        @media (max-width: 375px) {
            content: "";
            position: absolute;
            z-index: 1;
            bottom: 210px;
            left: -30px;
            right: -30px;
            background-image: url("../images/gradient-mobile-2.png");
            background-repeat: no-repeat;
            background-position: top center;      
            height: 800px;
        }
    }

    &__item {
        padding: 52px 30px 50px;
        position: relative;
        border: 2px solid transparent;
        border-radius: 30px;
        background-color: var(--background-color);
        @media (max-width: 1040px) {
            padding-inline: 15px;
        }

        &::before {
            content: "";
            margin: -3px;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: -1;
            border-radius: inherit;
            background: linear-gradient(180deg, #26a9e0 0%, #004aad 100%);
        }
    }

    &__image {
        width: 170px;
        @media (max-width: 1040px) {
            width: 120px;
        }
        @media (max-width: 840px) {
            width: 170px;
        }
    }

    &__title {
        max-width: 577px;
        margin-bottom: 75px;
    }
}
