.hero {
    padding-bottom: 87px;

    @media (max-width: 992px) {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    @media (max-width: 820px) {
        position: relative;
    }
    @media (max-width: 480px) {
        padding-top: 111px;
        padding-bottom: 62px;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__content {
        @media (max-width: 820px) {
            max-width: 420px;
        }
    }

    &__title {
        max-width: 552px;
        font-weight: 700;
        font-size: 44px;
        line-height: 70px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        margin-bottom: 33px;

        @media (max-width: 1200px) {
            font-size: 35px;
        }
        @media (max-width: 992px) {
            font-size: 30px;
            line-height: 50px;
        }
        @media (max-width: 480px) {
            font-size: 25px;
            line-height: 40px;
            margin-bottom: 71px;
        }
    }

    &__text {
        max-width: 450px;
        margin-bottom: 13px;
        @media (max-width: 480px) {
            margin-bottom: 15px;
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-top: 53px;
        @media (max-width: 540px) {
            flex-direction: column;
            align-items: start;

            .button {
                max-width: 282px;
                width: 100%;
            }
        }
        @media (max-width: 480px) {
            margin-top: 100px;
        }
    }

    &__image {
        @media (max-width: 992px) {
            width: 380px;
        }
        @media (max-width: 820px) {
            width: 300px;
            position: absolute;
            z-index: -1;
            top: 0;
            right: -30px;
        }
        @media (max-width: 480px) {
            top: 108px;
        }
    }
}
